	module.exports = {"data":{"post":{"id":"/Users/varya/WebDev/Personal/varya.github.com/content/life/expat-center-amsterdam/index_ru.md absPath of file >>> MarkdownRemark","html":"<p>Первое что нужно сделать по приезду в Голландию — посетить <a href=\"http://www.iamsterdam.com/en-GB/living/Expatcenter\">Expat center</a>. Это специальная организация, занимающаяся иммигрантами. Там можно получить BSN number — номер социального страхования, который требуется практически везде и прописаться по тому адресу, где мигрант поселился (даже если это временное жилье).\n<excerpt/></p>\n<p>Прием там ведется по записи. Если вы готовитесь приехать в Голландию, лучше чтобы кто-то (например HR) записал вас заранее. С собой нужно взять:</p>\n<ul>\n<li>паспорта</li>\n<li>апостилированные и переведенные свидетельства о рождении</li>\n<li>апостилированное и переведенное свидетельство о браке</li>\n<li>договор об аренде жилья</li>\n</ul>\n<p>Все процедуры занимаю около получаса. За это время вас прописывают, и вы оказываетесь в базе данных по адресам. Также вы получаете бумажку с номером BSN — он пригодится много где, например в банке. И ещё в паспорта вклеивают временные виды на жительство. Они очень-очень временные, нужны лишь на время изготовления пластиковой карточки.</p>\n<p>Услуги Expat-центра платные. Оформление knowledge migrant стоит около 250 евро, а членов его семьи — по 150 евро.</p>","fields":{"slug":"ru/life/expat-center-amsterdam/","prefix":"","disqusIdentifier":"life-expat-center-amsterdam-index-ru"},"frontmatter":{"title":"Expat-центр в Амстердаме","date":"27 December 2013","v2":true,"old":null,"tumblr":true,"meta":null,"cover":null}},"authornote":{"id":"/Users/varya/WebDev/Personal/varya.github.com/content/parts/author.md absPath of file >>> MarkdownRemark","html":"<p><strong>Mr. Gatsby</strong> Proin ornare ligula eu tellus tempus elementum. Aenean <a href=\"/\">bibendum</a> iaculis mi, nec blandit lacus interdum vitae. Vestibulum non nibh risus, a scelerisque purus. Blandit lacus interdum vitae. Vestibulum non nibh risus, a scelerisque purus.</p>"},"site":{"siteMetadata":{"siteUrl":"http://varya.me/"}}},"pathContext":{"slug":"ru/life/expat-center-amsterdam/","prev":{"id":"/Users/varya/WebDev/Personal/varya.github.com/content/life/little-suomi-29-2017/index_en.md absPath of file >>> MarkdownRemark","fields":{"slug":"en/life/little-suomi-29-2017/","prefix":"","lang":"en","disqusIdentifier":"life-little-suomi-29-2017-index-en"},"frontmatter":{"title":"Summer in Helsinki - citybikes, blueberries and everyday life"}},"next":{"id":"/Users/varya/WebDev/Personal/varya.github.com/content/life/life-in-finland-9/index_ru.md absPath of file >>> MarkdownRemark","fields":{"slug":"ru/life/life-in-finland-9/","prefix":"","lang":"ru","disqusIdentifier":"life-life-in-finland-9-index-ru"},"frontmatter":{"title":" Финляндия - самообслуживание в кафе, камеры хранение с холодильником, как сдают бутылки..."}}}}

